@import url(https://fonts.googleapis.com/css2?family=Spectral:wght@300&display=swap);
*{
    font-family: 'Spectral', serif;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.App{
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0%;
}

.navbar{
    top: 0;
    width: 100%;
    height: 100px;
    background-color: #fff;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    position: fixed;
}

.logo--navbar{
    height: 100%;

}

.logo--navbar img{
    height: 100%;
}
.links-navBar{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
}

.links-navBar span{
    margin-right: 18px;
    font-weight: bold;
    cursor: pointer;
    color: #293591;
}

.navBox{
    width: 70px;
    height: 60px;
    background-color: #293591;
}
.slider-container{
    position: relative;
    margin-top: 100px;
    width: 100%;
    height: 400px;
    background-color: aquamarine;

}

.slider-img{
    width: 100%;
    height: 100%;
}

.slider-img img{
    width: 100%;
    height: 100%;
}

.sliderSelector{
    position: absolute;
    bottom: 40px;
    left: 0%;
    width: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
}


.sliderItem{
    font-size: 16px;
    color: #fff;
    margin: 0 12px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.sliderItem > div{

    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #0e4f9d;
    cursor: pointer;
}

